import React, { memo, useEffect, useState } from "react";
import {
  ZoomableGroup,
  ComposableMap,
  Geographies,
  Geography,
  Sphere,
  Marker
} from "react-simple-maps";
import axios from 'axios';

const geoUrl =
  "https://raw.githubusercontent.com/zcreativelabs/react-simple-maps/master/topojson-maps/world-110m.json";

const WorldMap = ({ setTooltipContent, countries }) => {

  const paises= iso =>{
    console.log(iso);
    
    for (var i = 0; i < countries.length; i++){
      if(countries[i].countryInfo.iso2==iso){
        return countries[i].cases;
      }
    }
    
    
    return "Sin datos";
  }

  return (

    <ComposableMap data-tip="" projectionConfig={{ scale: 200 }}>
      
          <Geographies geography={geoUrl}>
            {({ geographies }) =>
              geographies.map(geo => (
                <Geography
                  key={geo.rsmKey}
                  geography={geo}
                  onMouseEnter={() => {
                    const { NAME, POP_EST, ISO_A2 } = geo.properties;
                    setTooltipContent(`${NAME} — Contagios: ${paises(ISO_A2)}`);
                  }}
                  onMouseLeave={() => {
                    setTooltipContent("");
                  }}
                  style={{
                    default: {
                      fill: "#FFFFFF",
                      outline: "#000000"
                    },
                    hover: {
                      fill: "#80bfff",
                      outline: "none"
                    },
                    pressed: {
                      fill: "#3399ff",
                      outline: "none"
                    }
                  }}
                />
              ))
            }
          </Geographies>
      </ComposableMap>
   
  );
}
export default memo(WorldMap);
