import React from 'react';
import Pag1 from './pag1';
import Pag2 from './pag';

const NavBar = () => {

    return (
        <>
            <nav className="navbar navbar-expand-md bg-dark navbar-dark fixed-top ">
                <a className="navbar-brand" href="/covid/">Coronavirus Tracker</a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsibleNavbar">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse justify-content-end" id="collapsibleNavbar">
                    <ul class="nav nav-tabs" role="tablist">
                        <li class="nav-item">
                            <a class="nav-link active" data-toggle="tab" href="#world">World</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" data-toggle="tab" href="#country">Country</a>
                        </li>
                    </ul>
                </div>
            </nav>
            <div class="tab-content">
                <div id="world" class="container-fluid tab-pane active"><br></br>
                    <Pag1 />
                </div>
                <div id="country" class="container-fluid tab-pane fade"><br></br>
                    <Pag2 />
                </div>
            </div>
        </>
    );
}
export default NavBar;