import React, {useState} from 'react';

const Search=({ getQuery })=>{
    const [text, setText] = useState('');

    const onChange = (q) =>{
        setText(q);
       
    }

    const handleChange=(text)=>{
        getQuery(text);
    }
    return(
        <div className='input-group mb-3 search'>
        
                <input type='text' className='form-control  input' placeholder='Buscar' 
                value= {text}
                onChange={(e)=>onChange(e.target.value)} autoFocus></input>
                <div className="input-group-append">
                <button className="btn btn-success" onClick={()=>handleChange(text)}>Buscar</button>
                </div>
        </div>
    )
}
export default Search;
