import React, { memo, useEffect, useState } from "react";
import WorldMap from './PAg1/WorldMap';
import InfectedDeath from './PAg1/InfectedDeath';
import axios from 'axios';
import Chart from './PAg1/chart';
import ReactTooltip from 'react-tooltip';

const Pag1 = () => {
    const [content, setContent] = useState("");
    const [items, setItems] = useState([]);
    const [left, setLeft] = useState([]);
  
    useEffect(() => {
      const fetchItems = async () => {
        const result = await axios(
          "https://disease.sh/v3/covid-19/all"
        );
        const izq =[ 
          {id: 'A', label: "Casos: ", cant: result.data.cases},
          {id: 'B', label: "Activos", cant: result.data.active},
          {id: 'C', label: "Muertes: ", cant: result.data.deaths},
          {id: 'D', label: "Criticos", cant: result.data.critical},
          {id: 'E', label: "Recuperados: ", cant: result.data.recovered}
        ];
        setLeft(izq);
        console.log(izq);
      }
      fetchItems();
    }, []);
  
    useEffect(() => {
      const fetchItems = async () => {
        const result = await axios(
          "https://disease.sh/v3/covid-19/countries?sort=cases"
        );
        console.log(result.data);
        setItems(result.data);
  
      }
      fetchItems();
    }, []);
  
    return (
        <div className="container-fluid">
            <div className="row">
                <div className="col-xl-2 data">
                    <h1 className="tituloWW">WorldWide: </h1>
                </div>
                
                {
                    left.map(left => (
                        <InfectedDeath key={left.id} item={left} />
                    ))
                }
    
            </div>

            <div className="row ">
                <div className="col-sm-8">
                    <WorldMap setTooltipContent={setContent} countries={items} />
                    <ReactTooltip>{content}</ReactTooltip>
                </div>
                <div className="col-sm-4" >

                    <div className="chart">
                        <Chart item={items}/>
                    </div>
                </div>

            </div>
        </div>
    );
}

export default Pag1;