import React, { useState, useEffect } from "react";
import numeral from 'numeral';
import { Line } from 'react-chartjs-2';
import Listas from './Lista';
import Listas2 from './listas2';




const Chart = ({ item }) => {
  const [CasesHistorico, setCasesHistorico] = useState({});
  const [DeathHistorico, setDeathHistorico] = useState({});

  const options1 = {
    legend: {
      display: false,
    },
    elements: {
      point: {
        radius: 0,
      },
    },
    maintainAspectRatio: false,
    tooltips: {
      mode: "index",
      intersect: false,
      callbacks: {
        label: function (tooltipItem, data) {
          return numeral(tooltipItem.value).format("+0,0");
        },
      },
    },
    scales: {
      xAxes: [
        {
          type: "time",
          time: {
            format: "MM/DD/YY",
            tooltipFormat: "ll",
          },
        },
      ],
      yAxes: [
        {
          gridLines: {
            display: false,
          },
          ticks: {
            // Include a dollar sign in the ticks
            callback: function (value, index, values) {
              return numeral(value).format("0a");
            },
          },
        },
      ],
    },
  };

  const options2 = {
    legend: {
      display: false,
    },
    elements: {
      point: {
        radius: 0,
      },
    },
    maintainAspectRatio: true,
    tooltips: {
      mode: "index",
      intersect: false,
      callbacks: {
        label: function (tooltipItem, DeathHistorico) {
          return numeral(tooltipItem.value).format("+0,0");
        },
      },
    },
    scales: {
      xAxes: [
        {
          type: "time",
          time: {
            format: "MM/DD/YY",
            tooltipFormat: "ll",
          },
        },
      ],
      yAxes: [
        {
          gridLines: {
            display: false,
          },
        },
      ],
    },
  };

  
  const buildChartData = (data, cases) => {
    const chartData = [];
    let lastDataPoint;
    for (let date in data.cases) {
      if (lastDataPoint) {
        let newDataPoint = {
          x: date,
          y: data[cases][date] - lastDataPoint
        }
        chartData.push(newDataPoint);
      }
      lastDataPoint = data[cases][date];
    }
    return chartData;

  }

  useEffect(() => {
    const fetchData = async () => {
      fetch("https://disease.sh/v3/covid-19/historical/all?lastdays=120")
        .then(response => response.json())
        .then(data => {
          console.log(data);
          const caseData = buildChartData(data, 'cases');
          setCasesHistorico(caseData);
          const deathData = buildChartData(data, 'deaths');
          setDeathHistorico(deathData);
        });
    }
    fetchData();
  }, [])


  return (
    <div>
      <ul className="nav nav-tabs nav-justified">
        <li className="nav-item">
          <a className="nav-link active" data-toggle="tab" href="#cases">Casos</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" data-toggle="tab" href="#death">Muertes</a>
        </li>
      </ul>

      <div className="tab-content">
        <div id="cases" className="container tab-pane active"><br></br>
        <div className="grafico">
          <h3 className="ChartTitle">Casos</h3>
          <div >
            {CasesHistorico?.length > 0 && (
              <Line 
              
                data={{
                  datasets: [
                    {
                      backgroundColor: "rgb(153, 204, 255,1)",
                      borderColor: "rgb(0, 153, 255,1)",
                      data: CasesHistorico,
                    },
                  ],
                }}
                options={options1}
              />
            )}
          </div>
          </div>
          <div>
            <div className="lista">
              <h5 className="titulo">Total de casos por país:</h5>
              <div className="container-fluid divC">
                <table className="table table-dark table-hover table-borderless table-responsive tabla">
                  <tbody>
                    {
                      item.map(item => (
                        <Listas key={item.countryInfo.iso3} item={item} />
                      ))
                    }
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>


        <div id="death" className="container tab-pane fade"><br></br>
        <div className="grafico">
        <h3 className="ChartTitle">Muertes</h3>
          <div >
            {DeathHistorico?.length > 0 && (
              <Line
                data={{
                  datasets: [
                    {
                      backgroundColor: "#ffcccc",
                      borderColor: "#ff4d4d",
                      data: DeathHistorico,
                    },
                  ],
                }}
                options={options2}
              />
            )}
          </div> 
          </div>
          <div>         
            <div className="lista">
              <h5 className="titulo">Total de muertes por pais</h5>
              <div className="container-fluid divM">
                <table className="table table-dark table-hover table-borderless table-responsive tabla">
                  <tbody>
                    {
                      item.map(item => (
                        <Listas2 key={item.countryInfo.iso3} item={item} />
                      ))
                    }
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>



    </div>
  );
}

export default Chart;