//https://disease.sh/v3/covid-19/historical/chile?lastdays=120

import React, { useState, useEffect } from "react";
import numeral from 'numeral';
import { Line } from 'react-chartjs-2';
import axios from 'axios';

const ChartPais = ({ query }) => {
  const [CasesHistorico, setCasesHistorico] = useState({});
  const [DeathHistorico, setDeathHistorico] = useState({});

  const options1 = {
    legend: {
      display: false,
    },
    elements: {
      point: {
        radius: 0,
      },
    },
    maintainAspectRatio: false,
    tooltips: {
      mode: "index",
      intersect: false,
      callbacks: {
        label: function (tooltipItem, data) {
          return numeral(tooltipItem.value).format("+0,0");
        },
      },
    },
    scales: {
      xAxes: [
        {
          type: "time",
          time: {
            format: "MM/DD/YY",
            tooltipFormat: "ll",
          },
        },
      ],
      yAxes: [
        {
          gridLines: {
            display: false,
          },
        },
      ],
    },
  };

  const options2 = {
    legend: {
      display: false,
    },
    elements: {
      point: {
        radius: 0,
      },
    },
    maintainAspectRatio: true,
    tooltips: {
      mode: "index",
      intersect: false,
      callbacks: {
        label: function (tooltipItem, DeathHistorico) {
          return numeral(tooltipItem.value).format("+0,0");
        },
      },
    },
    scales: {
      xAxes: [
        {
          type: "time",
          time: {
            format: "MM/DD/YY",
            tooltipFormat: "ll",
          },
        },
      ],
      yAxes: [
        {
          gridLines: {
            display: false,
          },
        },
      ],
    },
  };


  const buildChartData = (data, cases) => {
    const chartData = [];
    console.log("buildChart" + data.cases)
    let lastDataPoint;
    for (let date in data.cases) {
      if (lastDataPoint) {
        let newDataPoint = {
          x: date,
          y: data[cases][date] - lastDataPoint
        }
        chartData.push(newDataPoint);
      }

      lastDataPoint = data[cases][date];
    }
    return chartData;

  }
  /*JSON.stringify() */
  useEffect(() => {
    const fetchItems = async () => {
      console.log("query: " + query);
      try {
        const result = await axios(
          `https://disease.sh/v3/covid-19/historical/${query}?lastdays=120`
        )

        console.log(result.data.timeline);
        const data = result.data.timeline;
        console.log(data);
        const caseData = buildChartData(data, 'cases');
        setCasesHistorico(caseData);
        const deathData = buildChartData(data, 'deaths');
        setDeathHistorico(deathData);

      } catch (err) {
        console.log(err);
      }
    }
    fetchItems();
  }, [query]);

  return (
    <div className="row">
      <div className="col-sm-6 data">
        <div className="grafico">
          <h3 className="ChartTitle">Casos</h3>
          <div>
            {CasesHistorico?.length > 0 && (
              <Line
                data={{
                  datasets: [
                    {
                      backgroundColor: "rgb(153, 204, 255,1)",
                      borderColor: "rgb(0, 153, 255,1)",
                      data: CasesHistorico,
                    },
                  ],
                }}
                options={options2}
              />
            )}
          </div>
        </div>
      </div>
      <div className="col-sm-6 data">
        <div className="grafico">
          <h3 className="ChartTitle">Muertes</h3>
          <div>
            {DeathHistorico?.length > 0 && (
              <Line
                data={{
                  datasets: [
                    {
                      backgroundColor: "#ffcccc",
                      borderColor: "#ff4d4d",
                      data: DeathHistorico,
                    },
                  ],
                }}
                options={options2}
              />
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default ChartPais;