import React from 'react';
import ChartPais from './ChartPais';

const Informacion = ({ data, status }) => {

    if (status == 200) {
        return (
            <>
            <div className="container-fluid">
                <div className="row" id="pais">
                    <div className="col-xl-12 data">
                        <h2 className="paisLabel">País: {data.country}</h2>
                        <img src={data.countryInfo.flag} alt={data.country}></img>
                    </div>
                </div>
                <div className="row" id="casos">
                    <div className="col-xl-4 data">
                        <div className="card" id="A">
                            <div className="card-body">
                                <h4 className="card-title">Casos Confirmados</h4>
                                <p className="card-text">{data.cases}</p>
                            </div>
                        </div>
                    </div>
                    <div className="col-xl-4 data">
                        <div className="card" id="A">
                            <div className="card-body">
                                <h4 className="card-title">Confirmados hoy</h4>
                                <p className="card-text">{data.todayCases}</p>
                            </div>
                        </div>
                    </div>
                    <div className="col-xl-4 data">
                        <div className="card" id="A">
                            <div className="card-body">
                                <h4 className="card-title">Casos Activos</h4>
                                <p className="card-text">{data.active}</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row" id="muertes">
                    <div className="col-xl-4 data">
                        <div className="card" id="C">
                            <div className="card-body">
                                <h4 className="card-title">Pacientes Criticos</h4>
                                <p className="card-text">{data.critical}</p>
                            </div>
                        </div>
                    </div>
                    <div className="col-xl-4 data">
                        <div className="card" id="C">
                            <div className="card-body">
                                <h4 className="card-title">Muertes Confirmadas</h4>
                                <p className="card-text">{data.deaths}</p>
                            </div>
                        </div>
                    </div>
                    <div className="col-xl-4 data">
                        <div className="card" id="C">
                            <div className="card-body">
                                <h4 className="card-title">Muertes hoy</h4>
                                <p className="card-text">{data.todayDeaths}</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row" id="recover">
                    <div className="col-xl-4 data">
                        <div className="card" id="E">
                            <div className="card-body">
                                <h4 className="card-title">Recuperados</h4>
                                <p className="card-text">{data.recovered}</p>
                            </div>
                        </div>
                    </div>
                    <div className="col-xl-4 data">
                        <div className="card" id="E">
                            <div className="card-body">
                                <h4 className="card-title">Recuperados hoy</h4>
                                <p className="card-text">{data.todayRecovered}</p>
                            </div>
                        </div>
                    </div>
                    <div className="col-xl-4 data">
                        <div className="card" id="E">
                            <div className="card-body">
                                <h4 className="card-title">Test realizados</h4>
                                <p className="card-text">{data.tests}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div>
                <h2 className="chart2 paisLabel">Datos de los ultimos 4 meses</h2>
                <div className="ChartContenedor">
            <ChartPais query={data.country} />
            </div>
            </div>
            </>
        )
    }
    else if (status == 404) {
        return (
            <div className="container-fluid">
                <div className="row">
                    <div className="col-xl-12 img">
                        <img src="https://misago-project.org/media/avatars/53/7b/cvjwFUl9dKjCdi9T3fdvbaMCNRZNdSxb/DsyZV82wFi42Ia0405bcavUgvUj55Nrr.png" className="vector"></img>
                        <h2 className="error">El país no ha sido encontrado</h2>
                    </div>
                </div>
            </div>
        )
    }
    else {
        return (
            <div className="container-fluid inicio">
                <br></br>
                <div className="row ">
                    <div className="col-xl-12 ">
                        <h2 className="error">Busque un pais por su nombre</h2>
                    </div>
                </div>
            </div>
        )
    }
}
export default Informacion;
